import React, { useState } from 'react';
import StarScene from './StarScene';
import '../styles/App.css';

const SECTION_TITLES = ['About', 'Skills', 'Projects', 'Contact'];

const App: React.FC = () => {
    const [activeSection, setActiveSection] = useState<number>(0);

    return (
        <div className="app-root">
            <div className="canvas-wrap">
                <StarScene activeSection={activeSection} />
            </div>

            <div className="overlay">
                <nav className="overlay-nav">
                    {SECTION_TITLES.map((t, i) => (
                        <button key={t} className={`nav-item ${activeSection === i ? 'active' : ''}`} onClick={() => setActiveSection(i)}>
                            {t}
                        </button>
                    ))}
                </nav>

                <main className="content-wrapper">
                    {SECTION_TITLES.map((title, i) => (
                        <section key={title} className="page-section">
                            <h1>{title}</h1>
                            <p>This is the {title} section. Click the navigation buttons to move in 3D space between sections.</p>
                        </section>
                    ))}
                </main>
            </div>
        </div>
    );
};

export default App;